<template>
  <div class="story__content">
    <header class="create">
      <Button
              :classList="['btn', 'btn__primary-2', 'story__back']"
              @clicked="back"
              :label="$ml.get('create.memorial.common.buttons.back')"
      >
        <span slot="icon-left" class="icon icon__arrow-3 btn-arrow"></span>
      </Button>

      <router-link :to="{ name: 'home' }">
        <img class="story__logo" src="~@/assets/img/icon/welcome-screen-logo.svg" alt="Logo">
      </router-link>
    </header>

    <div>
      <div class="post-plan__title-wrapper">
        <h1 class="post-plan__title third-header">{{ $ml.get('create.memorial.step3.title') }}</h1>
        <div class="post-plan__decor"></div>
        <p class="create-memorial__title-text create-memorial__title-text--padding second-text">
          {{ $ml.get('create.memorial.step3.description') }}
        </p>
      </div>
      <div class="create-memorial__form">
        <div class="post-plan__wrapper">

          <div class="post-plan__card-conatiner">
            <!-- payment-card -->
            <div
                    :class="['payment-card', 'payment-card--another', {'active': 'monthly' === subscription.type}]"
                    @click.prevent="setSubscriptionType('monthly')"
            >
              <div class="payment-card__top">
                <p class="payment-card__title second-text">{{ $ml.get('payments.monthly.title') }}</p>
              </div>

              <div class="payment-card__bottom">
                <div class="payment-card__price-container">
                  <div class="payment-card__price-amount">{{ $ml.get('payments.monthly.price') }}</div>
                  <div class="payment-card__price-payments"><span>&#47;</span>{{ $ml.get('payments.monthly.term') }}</div>
                </div>
                <div class="payment-card__entirely">{{ $ml.get('payments.monthly.desc') }}</div>

                <ul class="payment-card__list">
                  <li class="payment-card__item">
                    <img src="~@/assets/img/icon/payment-card-checked.svg" alt="">
                    {{ $ml.get('payments.monthly.features.0') }}
                  </li>
                  <li class="payment-card__item">
                    <img src="~@/assets/img/icon/payment-card-checked.svg" alt="">
                    {{ $ml.get('payments.monthly.features.1') }}
                  </li>
                  <li class="payment-card__item">
                    <img src="~@/assets/img/icon/payment-card-checked.svg" alt="">
                    {{ $ml.get('payments.monthly.features.2') }}
                  </li>
                </ul>
              </div>

              <div class="payment-card__box-btn">
                <button class="payment-card__btn">
                  {{ subscription.type === 'monthly' ? $ml.get('create.memorial.step3.subscription.oneTime.btn') : $ml.get('create.memorial.step3.subscription.monthly.btn') }}
                </button>
              </div>
            </div>

            <!-- payment-card -->
            <div
                    :class="['payment-card', 'payment-card--another', {'active': 'once' === subscription.type}]"
                    @click.prevent="setSubscriptionType('once')"
            >
              <div class="payment-card__top">
                <p class="payment-card__title second-text">
                  {{ $ml.get('payments.once.title') }}
                </p>
              </div>

              <div class="payment-card__bottom">
                <div class="payment-card__price-container">
                  <div class="payment-card__price-amount">{{ $ml.get('payments.once.price') }}</div>
                  <div class="payment-card__price-payments"><span>&#47;</span>
                    {{ $ml.get('payments.once.term') }}
                  </div>
                </div>
                <div class="payment-card__entirely">{{ $ml.get('payments.once.desc') }}</div>

                <ul class="payment-card__list">
                  <li class="payment-card__item">
                    <img src="~@/assets/img/icon/payment-card-checked.svg" alt="">
                    {{ $ml.get('payments.once.features.0') }}
                  </li>
                  <li class="payment-card__item">
                    <img src="~@/assets/img/icon/payment-card-checked.svg" alt="">
                    {{ $ml.get('payments.once.features.1') }}
                  </li>
                  <li class="payment-card__item">
                    <img src="~@/assets/img/icon/payment-card-checked.svg" alt="">
                    {{ $ml.get('payments.once.features.2') }}
                  </li>
                </ul>
              </div>

              <div class="payment-card__box-btn">
                <button class="payment-card__btn">
                  {{ subscription.type === 'once' ? $ml.get('create.memorial.step3.subscription.oneTime.btn') : $ml.get('create.memorial.step3.subscription.monthly.btn') }}
                </button>
              </div>
            </div>
          </div>

          <div class="post-plan__options">
            <div class="post-plan__options-title">
              {{ $ml.get('create.memorial.step3.subscription.options.title') }}
            </div>
            <div class="post-plan__options-wrapper">
              <div
                      @click.prevent="setSubscriptionInstallation('independent')"
                      :class="['post-plan__options-item', 'post-plan__options-second', {'active': 'independent' === subscription.installation}]"
              >{{ $ml.get('create.memorial.step3.subscription.options.independent') }}
              </div>
              <div
                      @click.prevent="setSubscriptionInstallation('team')"
                      :class="['post-plan__options-item', 'post-plan__options-first', {'active': 'team' === subscription.installation}]"
              >{{ $ml.get('create.memorial.step3.subscription.options.team.label') }}
                <span>{{ $ml.get('create.memorial.step3.subscription.options.team.info') }}</span>
                <img class="post-plan__options-img" src="~@/assets/img/icon/ask-yelow.svg" alt="" @click="isTooltipShow = !isTooltipShow">
              </div>
            </div>
          </div>

          <div class="form__btn-wrapper">
            <button @click.prevent="getIframe()" type="button" class="form__button default-second-btn">
              <img class="arrow-left" src="~@/assets/img/icon/arrow-left.svg">
              {{ $ml.get('create.memorial.step3.buttons.start') }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- //    body  -->
    <div class="payment-popup" v-if="showIframeModal && iframeUrl">
      <div class="payment-popup__content" v-click-outside="onClickOutside">
        <div class="payment-popup__top">
          <img src="~@/assets/img/icon/payment-pop-close.svg" alt="" @click.prevent="closeIframe();">
          <span>{{ $ml.get('create.memorial.step3.paymentTitle') }}</span>
        </div>
        <div class="payment-popup__inner">
          <iframe :src="iframeUrl" style="height: 550px;"></iframe>
        </div>
      </div>
    </div>

    <Tooltip
        v-if="isTooltipShow"
        :label="$ml.get('payments.help.label')"
        :withTalkLink="false"
        @close="isTooltipShow = false"
    >
      <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get('payments.help.text') }}
        </span>
      </div>
    </Tooltip>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import Button from '@/components/Button';
import Tooltip from "@/components/Tooltip";

export default {
  name: "Payment",
  components: {
    Button,
    Tooltip,
  },
  data() {
    return {
      showIframeModal: false,
      isTooltipShow: false,
    };
  },
  computed: {
    ...mapGetters({
      memorial: 'story/memorial',
      subscription: 'memorial/getSubscription',
      iframeUrl: 'memorial/getIframeUrl',
    }),
  },
  methods: {
    back() {
        this.$router.push({name: 'memorials.edit', params: {id: this.memorial.id}});
    },
    onClickOutside() {
      this.closeIframe();
    },
    async getIframe() {
      await this.$store.dispatch('memorial/updatePaymentPlan', this.memorial.id);
      await this.showIframe();
    },
    showIframe() {
      this.showIframeModal=true;
      document.querySelector('body').classList.add('payment-popup-open');
    },
    closeIframe() {
      this.showIframeModal=false;
      document.querySelector('body').classList.remove('payment-popup-open');
    },
    setSubscriptionType(type) {
      this.$store.dispatch('memorial/setSubscriptionType', type);
    },
    setSubscriptionInstallation(type) {
      this.$store.dispatch('memorial/setSubscriptionInstallation', type);
    },
    goTo(route, params = null, step = null) {
      this.$router.push({name: route, params});
    },
    goToMemorial() {
      this.$router.push({name: 'memorials.edit', params: {id: this.memorial.id}})
    },
  },
  async mounted() {
    await this.$store.dispatch('story/getOwnerMemorial', this.$route.params.slug);
  },
}
</script>

<style lang="scss">
  @import "~@/assets/scss/pages/create-story.scss";
  @import '~@/assets/scss/components/form';
  @import '~@/assets/scss/components/choose-post-plan';
  @import '~@/assets/scss/components/payment-card';
  @import '~@/assets/scss/components/payment-popup';
  @import '~@/assets/scss/components/create-memorial';

  .form__btn-wrapper button:first-child {
     margin-bottom: 0px;
  }
</style>