<template>
    <section class="story">
        <component :is="step"
        ></component>

        <Sidebar v-if="memorial"
                :relativeUsers="relativeUsers"
                :step="step"
                :key="memorial.id"
        />

        <HelpCenter/>
    </section>
</template>

<script>

    import {mapGetters} from 'vuex';
    import HelpCenter from '@/components/HelpCenter.vue';
    import Sidebar from '@/views/memorials/story/partials/Sidebar';
    import Payment from '@/views/memorials/story/steps/Payment';

    export default {
        name: "MemorialPayment",
        components: {
            Sidebar,
            HelpCenter,
            Payment,
        },
        data() {
            return {
                step: 'payment',
            };
        },
        methods: {
        },
        computed: {
            ...mapGetters({
                isMemorialExist: 'story/isMemorialExist',
                memorial: 'story/memorial',
            }),
            relativeUsers() {
                const relativeUsers = this.$store.getters['story/relativeUsers'];

                return  {
                    count: relativeUsers.length,
                    data: relativeUsers.splice(0, 4),
                };
            },
        },
        async created() {
            await this.$store.dispatch('story/getOwnerMemorial', this.$route.params.slug);

            if (!this.isMemorialExist) {
                //todo: translate
                await this.$router.push({name: 'home'});
            }
            else if(this.memorial.paid) {
                await this.$router.push({name: 'memorials.edit', params: {id: this.memorial.id}})
            }
        },
    }
</script>

<style scoped lang="scss">
    @import "~@/assets/scss/pages/create-story.scss";
</style>